import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./header.scss";
import logo from "../../assets/img/logo.png";

export default function Header() {
  const [isFixed, setIsFixed] = useState(false);
  const [isSearchActive, setIsSearchActive] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 10) {
        setIsFixed(true);
      } else {
        setIsFixed(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  // Toggle search button click
  const handleSearchToggle = () => {
    setIsSearchActive(!isSearchActive);
  };

  return (
    <header>
      <nav
        className={`header_top ${isFixed ? "fixed" : ""} ${
          isSearchActive ? "expanded" : ""
        }`}
      >
        <div className="width wrap">
          <span className="material-symbols-rounded pc_ui">menu</span>
          <h1>
            <Link to="/">
              <img src={logo} alt="logo" />
            </Link>
          </h1>
          <input
            id="user-search"
            type="search"
            placeholder="  "
            aria-label="  "
          />
          <div className="button_area pc_ui">
            <span className="material-symbols-outlined" title=" ">
              person
            </span>
            <span className="material-symbols-outlined" title="">
              shopping_cart
            </span>
            <span className="material-symbols-outlined" title=" ">
              list_alt
            </span>
          </div>
        </div>
      </nav>
      <nav className="header_bottom">
        <div className="mob_tap between width side">
          <ul className="between gap">
            <li>
              <Link to="/rocket-delivery"></Link>
            </li>
            <li>
              <Link to="/rocket-import"></Link>
            </li>
            <li>
              <Link to="/best"></Link>
            </li>
            <li>
              <Link to="/special-offers"></Link>
            </li>
            <li>
              <Link to="/events-coupons">/</Link>
            </li>
          </ul>
          <ul className="between gap pc_ui">
            <li>
              <Link to="/login"></Link>
            </li>
            <li>
              <Link to="/signup"></Link>
            </li>
            <li>
              <Link to="/customer-service"></Link>
            </li>
          </ul>
        </div>
      </nav>
      <nav className="bot side center between mob_ui">
        <span className="material-symbols-rounded">menu</span>
        <span
          className="material-symbols-outlined"
          title=""
          onClick={handleSearchToggle}
        >
          search
        </span>
        <span className="material-symbols-outlined" title=" ">
          person
        </span>
        <span className="material-symbols-outlined" title="">
          shopping_cart
        </span>
        <span className="material-symbols-outlined" title=" ">
          list_alt
        </span>
      </nav>
    </header>
  );
}
